import {StoreController} from '../../../../store/controllers/StoreController';

import {computed} from 'vue';
import {SetupLinkIndexReturn} from '../../common/LinkIndex';
export function SetupNodeNavigation(setupLinkIndexReturn: SetupLinkIndexReturn) {
	const current_node_graph_id = computed(() =>
		StoreController.editor.current_node_graph_id(setupLinkIndexReturn.linkIndex.value)
	);
	const display_go_up_button = computed(() => {
		if (current_node_graph_id == null) {
			return false;
		}
		const node = StoreController.editor.currentNode(setupLinkIndexReturn.linkIndex.value);
		if (!node) {
			return false;
		}
		return node.parent() != null;
	});

	function go_up() {
		StoreController.editor.goUp(setupLinkIndexReturn.linkIndex.value);
	}

	return {
		display_go_up_button,
		go_up,
	};
}
